	module.exports = {"pathContext":{"docHtml":"<h2>V8 internals</h2>\n<p>The hooks to access V8 internals—including GC and statistics—are different across the supported versions of V8, therefore NAN provides its own hooks that call the appropriate V8 methods.</p>\n<ul>\n<li><a href=\"#api_nan_gc_callback\"><b><code>NAN<em>GC</em>CALLBACK()</code></b></a></li>\n<li><a href=\"#api_nan_add_gc_epilogue_callback\"><b><code>Nan::AddGCEpilogueCallback()</code></b></a></li>\n<li><a href=\"#api_nan_remove_gc_epilogue_callback\"><b><code>Nan::RemoveGCEpilogueCallback()</code></b></a></li>\n<li><a href=\"#api_nan_add_gc_prologue_callback\"><b><code>Nan::AddGCPrologueCallback()</code></b></a></li>\n<li><a href=\"#api_nan_remove_gc_prologue_callback\"><b><code>Nan::RemoveGCPrologueCallback()</code></b></a></li>\n<li><a href=\"#api_nan_get_heap_statistics\"><b><code>Nan::GetHeapStatistics()</code></b></a></li>\n<li><a href=\"#api_nan_set_counter_function\"><b><code>Nan::SetCounterFunction()</code></b></a></li>\n<li><a href=\"#api_nan_set_create_histogram_function\"><b><code>Nan::SetCreateHistogramFunction()</code></b></a></li>\n<li><a href=\"#api_nan_set_add_histogram_sample_function\"><b><code>Nan::SetAddHistogramSampleFunction()</code></b></a></li>\n<li><a href=\"#api_nan_idle_notification\"><b><code>Nan::IdleNotification()</code></b></a></li>\n<li><a href=\"#api_nan_low_memory_notification\"><b><code>Nan::LowMemoryNotification()</code></b></a></li>\n<li><a href=\"#api_nan_context_disposed_notification\"><b><code>Nan::ContextDisposedNotification()</code></b></a></li>\n<li><a href=\"#api_nan_get_internal_field_pointer\"><b><code>Nan::GetInternalFieldPointer()</code></b></a></li>\n<li><a href=\"#api_nan_set_internal_field_pointer\"><b><code>Nan::SetInternalFieldPointer()</code></b></a></li>\n<li><a href=\"#api_nan_adjust_external_memory\"><b><code>Nan::AdjustExternalMemory()</code></b></a></li>\n</ul>\n<p><a name=\"api_nan_gc_callback\"></a></p>\n<h3>NAN<em>GC</em>CALLBACK(callbackname)</h3>\n<p>Use <code>NAN_GC_CALLBACK</code> to declare your callbacks for <code>Nan::AddGCPrologueCallback()</code> and <code>Nan::AddGCEpilogueCallback()</code>. Your new method receives the arguments <code>v8::GCType type</code> and <code>v8::GCCallbackFlags flags</code>.</p>\n<pre><code class=\"language-c++\">static Nan::Persistent&#x3C;Function> callback;\n\nNAN_GC_CALLBACK(gcPrologueCallback) {\n  v8::Local&#x3C;Value> argv[] = { Nan::New(\"prologue\").ToLocalChecked() };\n  Nan::MakeCallback(Nan::GetCurrentContext()->Global(), Nan::New(callback), 1, argv);\n}\n\nNAN_METHOD(Hook) {\n  callback.Reset(To&#x3C;Function>(args[0]).ToLocalChecked());\n  Nan::AddGCPrologueCallback(gcPrologueCallback);\n  info.GetReturnValue().Set(info.Holder());\n}\n</code></pre>\n<p><a name=\"api_nan_add_gc_epilogue_callback\"></a></p>\n<h3>Nan::AddGCEpilogueCallback()</h3>\n<p>Signature:</p>\n<pre><code class=\"language-c++\">void Nan::AddGCEpilogueCallback(v8::Isolate::GCEpilogueCallback callback, v8::GCType gc_type_filter = v8::kGCTypeAll)\n</code></pre>\n<p>Calls V8's <a href=\"https://v8docs.nodesource.com/io.js-3.3/d5/dda/classv8_1_1_isolate.html#a90d1860babc76059c62514b422f56960\"><code>AddGCEpilogueCallback()</code></a>.</p>\n<p><a name=\"api_nan_remove_gc_epilogue_callback\"></a></p>\n<h3>Nan::RemoveGCEpilogueCallback()</h3>\n<p>Signature:</p>\n<pre><code class=\"language-c++\">void Nan::RemoveGCEpilogueCallback(v8::Isolate::GCEpilogueCallback callback)\n</code></pre>\n<p>Calls V8's <a href=\"https://v8docs.nodesource.com/io.js-3.3/d5/dda/classv8_1_1_isolate.html#a05c60859fd4b8e96bfcd451281ed6c7c\"><code>RemoveGCEpilogueCallback()</code></a>.</p>\n<p><a name=\"api_nan_add_gc_prologue_callback\"></a></p>\n<h3>Nan::AddGCPrologueCallback()</h3>\n<p>Signature:</p>\n<pre><code class=\"language-c++\">void Nan::AddGCPrologueCallback(v8::Isolate::GCPrologueCallback, v8::GCType gc_type_filter callback)\n</code></pre>\n<p>Calls V8's <a href=\"https://v8docs.nodesource.com/io.js-3.3/d5/dda/classv8_1_1_isolate.html#ab4b87b8f9f8e5bf95eba4009357e001f\"><code>AddGCPrologueCallback()</code></a>.</p>\n<p><a name=\"api_nan_remove_gc_prologue_callback\"></a></p>\n<h3>Nan::RemoveGCPrologueCallback()</h3>\n<p>Signature:</p>\n<pre><code class=\"language-c++\">void Nan::RemoveGCPrologueCallback(v8::Isolate::GCPrologueCallback callback)\n</code></pre>\n<p>Calls V8's <a href=\"https://v8docs.nodesource.com/io.js-3.3/d5/dda/classv8_1_1_isolate.html#a9f6c51932811593f81ff30b949124186\"><code>RemoveGCEpilogueCallback()</code></a>.</p>\n<p><a name=\"api_nan_get_heap_statistics\"></a></p>\n<h3>Nan::GetHeapStatistics()</h3>\n<p>Signature:</p>\n<pre><code class=\"language-c++\">void Nan::GetHeapStatistics(v8::HeapStatistics *heap_statistics)\n</code></pre>\n<p>Calls V8's <a href=\"https://v8docs.nodesource.com/io.js-3.3/d5/dda/classv8_1_1_isolate.html#a5593ac74687b713095c38987e5950b34\"><code>GetHeapStatistics()</code></a>.</p>\n<p><a name=\"api_nan_set_counter_function\"></a></p>\n<h3>Nan::SetCounterFunction()</h3>\n<p>Signature:</p>\n<pre><code class=\"language-c++\">void Nan::SetCounterFunction(v8::CounterLookupCallback cb)\n</code></pre>\n<p>Calls V8's <a href=\"https://v8docs.nodesource.com/io.js-3.3/d5/dda/classv8_1_1_isolate.html#a045d7754e62fa0ec72ae6c259b29af94\"><code>SetCounterFunction()</code></a>.</p>\n<p><a name=\"api_nan_set_create_histogram_function\"></a></p>\n<h3>Nan::SetCreateHistogramFunction()</h3>\n<p>Signature:</p>\n<pre><code class=\"language-c++\">void Nan::SetCreateHistogramFunction(v8::CreateHistogramCallback cb) \n</code></pre>\n<p>Calls V8's <a href=\"https://v8docs.nodesource.com/io.js-3.3/d5/dda/classv8_1_1_isolate.html#a542d67e85089cb3f92aadf032f99e732\"><code>SetCreateHistogramFunction()</code></a>.</p>\n<p><a name=\"api_nan_set_add_histogram_sample_function\"></a></p>\n<h3>Nan::SetAddHistogramSampleFunction()</h3>\n<p>Signature:</p>\n<pre><code class=\"language-c++\">void Nan::SetAddHistogramSampleFunction(v8::AddHistogramSampleCallback cb) \n</code></pre>\n<p>Calls V8's <a href=\"https://v8docs.nodesource.com/io.js-3.3/d5/dda/classv8_1_1_isolate.html#aeb420b690bc2c216882d6fdd00ddd3ea\"><code>SetAddHistogramSampleFunction()</code></a>.</p>\n<p><a name=\"api_nan_idle_notification\"></a></p>\n<h3>Nan::IdleNotification()</h3>\n<p>Signature:</p>\n<pre><code class=\"language-c++\">void Nan::IdleNotification(v8::HeapStatistics *heap_statistics)\n</code></pre>\n<p>Calls V8's <a href=\"https://v8docs.nodesource.com/io.js-3.3/d5/dda/classv8_1_1_isolate.html#ad6a2a02657f5425ad460060652a5a118\"><code>IdleNotification()</code> or <code>IdleNotificationDeadline()</code></a> depending on V8 version.</p>\n<p><a name=\"api_nan_low_memory_notification\"></a></p>\n<h3>Nan::LowMemoryNotification()</h3>\n<p>Signature:</p>\n<pre><code class=\"language-c++\">void Nan::LowMemoryNotification() \n</code></pre>\n<p>Calls V8's <a href=\"https://v8docs.nodesource.com/io.js-3.3/d5/dda/classv8_1_1_isolate.html#a24647f61d6b41f69668094bdcd6ea91f\"><code>LowMemoryNotification()</code></a>.</p>\n<p><a name=\"api_nan_context_disposed_notification\"></a></p>\n<h3>Nan::ContextDisposedNotification()</h3>\n<p>Signature:</p>\n<pre><code class=\"language-c++\">void Nan::ContextDisposedNotification()\n</code></pre>\n<p>Calls V8's <a href=\"https://v8docs.nodesource.com/io.js-3.3/d5/dda/classv8_1_1_isolate.html#ad7f5dc559866343fe6cd8db1f134d48b\"><code>ContextDisposedNotification()</code></a>.</p>\n<p><a name=\"api_nan_get_internal_field_pointer\"></a></p>\n<h3>Nan::GetInternalFieldPointer()</h3>\n<p>Gets a pointer to the internal field with at <code>index</code> from a V8 <code>Object</code> handle.</p>\n<p>Signature:</p>\n<pre><code class=\"language-c++\">void* Nan::GetInternalFieldPointer(v8::Local&#x3C;v8::Object> object, int index) \n</code></pre>\n<p>Calls the Object's <a href=\"https://v8docs.nodesource.com/io.js-3.3/db/d85/classv8_1_1_object.html#ab3c57184263cf29963ef0017bec82281\"><code>GetAlignedPointerFromInternalField()</code> or <code>GetPointerFromInternalField()</code></a> depending on the version of V8.</p>\n<p><a name=\"api_nan_set_internal_field_pointer\"></a></p>\n<h3>Nan::SetInternalFieldPointer()</h3>\n<p>Sets the value of the internal field at <code>index</code> on a V8 <code>Object</code> handle.</p>\n<p>Signature:</p>\n<pre><code class=\"language-c++\">void Nan::SetInternalFieldPointer(v8::Local&#x3C;v8::Object> object, int index, void* value)\n</code></pre>\n<p>Calls the Object's <a href=\"https://v8docs.nodesource.com/io.js-3.3/d5/dda/classv8_1_1_isolate.html#ad7f5dc559866343fe6cd8db1f134d48b\"><code>SetAlignedPointerInInternalField()</code> or <code>SetPointerInInternalField()</code></a> depending on the version of V8.</p>\n<p><a name=\"api_nan_adjust_external_memory\"></a></p>\n<h3>Nan::AdjustExternalMemory()</h3>\n<p>Signature:</p>\n<pre><code class=\"language-c++\">int Nan::AdjustExternalMemory(int bytesChange)\n</code></pre>\n<p>Calls V8's <a href=\"https://v8docs.nodesource.com/io.js-3.3/d5/dda/classv8_1_1_isolate.html#ae1a59cac60409d3922582c4af675473e\"><code>AdjustAmountOfExternalAllocatedMemory()</code></a>.</p>","docPath":"argo-ci/node_modules/nan/doc/v8_internals","proj":"argo-ci"}}

